

@font-face {
    font-family: permenantMarker;
    src: url('../assets/PermanentMarker-Regular.ttf');
}

.font-myfirstFont {
    font-family: permenantMarker;
    font-size: 80px;
}

.block-we-have-brands {
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
}

.block-we-have-brands>div {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    align-items: center;
    list-style: none;
    width: 100%;
}


.block-we-have-brands>div> span {
   
    width: 100%;
}


.block-we-have-brands img {
    text-align: center; 
    /* inline */
    vertical-align:middle;
    width: 100%;
    height: 100%;
}


.block-we-have-brands img::after {
    position: absolute;
    /* must need content */
    content: "";
    /* 
    
    doesnt work on images

    The before and after pseudo-selectors don't insert HTML elements â€” they insert text before or after the existing content of 
    the targeted element. Because image elements don't contain text or have descendants, neither img:before or img:after will do 
    you any good. This is also the case for elements like <br> and <hr> for the same reason.

    */
    background-color: aliceblue;

}

