.component__carousel{ 
    /* 
        The containing block for an absolutely positioned 
        element is established by its closest, positioned ancestor. That is,
        the nearest element outside it that has a position of absolute, 
        relative or fixed. If there is no such ancestor element, the 
        initial containing block (the browser window) is used. 
    */
    position: relative;
    padding: 0 10%;
    }
    
    .component__carousel > div.imageWrapper > img{
        width: 100%;
        height: 500px;
    }
    div.textCarousel{
        width: 100%;
        position: absolute;
        top: 70%;
        left:0;
        color: black;
        z-index: 100;
    }
    
    div.textCarousel h1, div.textCarousel h2, div.textCarousel h3, 
    div.textCarousel h4, div.textCarousel h5{
        font-weight: 800;
        font-size: 2rem;
    }
    div.textCarousel p{
        
        font-weight: 800;
        font-size: 1.5rem;
    }
    
    .left, .right{
        color: grey;
        font-weight: bold;
        position: absolute;  
        /* used in conjunction with position: relative */
        z-index: 100;
        top: 40%;
        cursor: pointer;
    }
    
  
    div.imageWrapper img{
        opacity: 0.8;
    }
    
    @media only screen and (max-width: 750px) {
        .component__carousel{ 
            padding: 0;
            }
        .component__carousel > div.imageWrapper{
                overflow-x: hidden;
        }
        .component__carousel > div.imageWrapper > img{
            height: 40vw;
            width: 150%;
            position: relative;
            left: -20vw;
            top: 10vh;
    
        }
        .left{
            padding: 100px;        }
        .right{
            padding: -100px;

        }
      }
    
      @media only screen and (max-width: 900px) {
        .component__carousel > div.imageWrapper > img{
            height: 400px;
           
        }
        .left{
            padding: 100px;        }
        .right{
            padding: -100px;

        }
      }